html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'YS Text', 'HelveticaNeue', 'Arial', 'Helvetica', sans-serif;
  font-size: 13px;
  line-height: 1.4;
  overflow: hidden;
}

strong {
  font-weight: 500;
}

:root {
  --dark-grey: #444451;
  --dark-grey-2: #36363F;
  --disable-color-text: #E8E8E8;
  --table-header-background: #F2F2F2;
  --black-color-text: #333333;
  --white-color-text: #FFFFFF;
  --grey-color-text: #858585;
  --action-color: #F8CC46;
  --button-color-text: #36363F;

  .button2_view_classic.button2_theme_action:before {
    background-color: var(--action-color);
  }

  .button2__text {
    color: var(--button-color-text);
  }
}



