@import "@yandex-data-ui/common/build/components/Toaster/constants/constants.scss";

.#{$cNameToaster} {
  bottom: unset;
  top: 10px;
}


.ch-kit {
  display: flex;
  width: 100vw;
  height: calc(100vh - 56px);
  flex-direction: column;
  box-sizing: border-box;
  color: var(--yc-color-text-primary);
  background: var(--yc-color-base);
  position: relative;

  &__content {
    height: calc(100% - 36px);
    position: relative;
  }
}

.Resizer {
  background: var(--yc-color-divider);
  z-index: 1;
  box-sizing: border-box;
  background-clip: padding-box;
  flex-shrink: 0;
}

.Resizer:hover {
  transition: all 2s ease;
}

.Resizer.horizontal {
  height: 11px;
  margin: -5px 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  cursor: row-resize;
  width: 100%;
}

.Resizer.horizontal:hover {
  border-top: 5px solid var(--yc-color-hover-area);
  border-bottom: 5px solid var(--yc-color-hover-area);
}

.Resizer.vertical {
  width: 11px;
  margin: 0 -5px;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  cursor: col-resize;
}

.Resizer.vertical:hover {
  border-left: 5px solid var(--yc-color-hover-area);
  border-right: 5px solid var(--yc-color-hover-area);
}

.Resizer.disabled {
  cursor: not-allowed;
}

.Resizer.disabled:hover {
  border-color: transparent;
}

