.user2__add-account .user-account__name {
    font-size: 13px;
    line-height: 16px;
    white-space: normal;
}

.user2__add-account .user-account__pic:before {
    position: absolute;
    top: -1px;
    right: -1px;
    bottom: -1px;
    left: -1px;

    content: '';

    opacity: .6;
    border: 1px solid #b3b3b3;
    border-radius: 50px;
    background: #ccc url(user2__add-account.assets/add-white.svg) center no-repeat;
}

.user2__accounts .user2__add-account_hidden_yes {
    display: none;
}
