.yc-header {
    &__logo-wrapper {
        position: relative;
        display: flex;
        align-items: center;
    }

    &__logo-icon {
        flex: 0 0 36px;
        margin: 0 6px;
        text-align: center;
    }

    &__logo-svg-place {
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        display: flex;
        align-items: center;
    }

    &__logo-link {
        &, &:hover, &:active, &:visited, &:focus {
            text-decoration: none;
            outline: none;
            color: inherit;
        }
    }
}
