.yc-datepicker-months {
    display: grid;
    grid-template-columns: 51px repeat(3, 72px);
    grid-template-rows: repeat(4, 40px);
    grid-gap: 4px;
    justify-content: center;
    margin-bottom: 20px;

    &__month {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        border-radius: 4px;
        cursor: pointer;

        &:hover {
            background-color: var(--yc-color-hover-area);
        }

        &_today {
            color: var(--yc-color-selection-accent);
        }

        &_selected {
            border-radius: 0;
            background-color: var(--yc-color-selection);

            &:hover {
                background-color: var(--yc-color-selection);
            }
        }

        &_left-edge {
            border-top-left-radius: 4px;
            border-bottom-left-radius: 4px;
        }

        &_right-edge {
            border-top-right-radius: 4px;
            border-bottom-right-radius: 4px;
        }

        &_selected:not(&_left-edge)::before {
            content: '';
            position: absolute;
            top: 0;
            left: -4px;
            display: block;
            height: 40px;
            width: 4px;
            background-color: var(--yc-color-selection);
        }

        &_disabled {
            opacity: 0.3;
            pointer-events: none;
        }
    }

    &__title {
        align-self: center;
        font-size: 18px;
        line-height: 20px;
        color: var(--yc-color-contrast-50-solid);
        cursor: pointer;

        &_disabled {
            opacity: 0.3;
            pointer-events: none;
        }
    }
}
