.yc-navigation {
    &__entries {
        display: flex;
        flex-direction: column;
        flex: 1 1 100%;
        overflow: hidden;
    }

    &__entries-header {
        flex: 0 0 auto;
        padding: 0 20px 12px;
        display: flex;
    }

    &__search {
        flex: 0 1 400px;
        margin-right: 20px;
    }

    &__custom {
        margin-left: auto;

        &:empty {
            display: none;
        }
        &:not(:empty) {
            display: flex;
        }
    }

    &__view {
        flex: 1 1 100%;
    }

    &__table-view {
        height: 100%;
    }

    &__spinner {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__empty-entries {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 20px;
        color: var(--yc-color-text-inactive);
    }

    &__filters {
        display: flex;
        align-items: center;
        flex-shrink: 0;
    }

    &__filters-item {
        margin-right: 20px;
    }
}
