@mixin button2_theme_flat {
    .button2_theme_flat {
        position: relative;
        z-index: 0;

        color: var(--yc-color-text-primary);
        border: none;
        outline: none;
    }

    .button2_view_classic.button2_theme_flat:before {
        border-radius: 3px;
    }

    .button2_theme_flat.button2_checked_yes:not(.button2_disabled_yes) .button2__text,
    .button2_theme_flat.button2_checked_yes:not(.button2_disabled_yes) .button2__icon {
        opacity: 0.8;
    }

    .utilityfocus .button2_view_classic.button2_theme_flat.button2_focused_yes:before {
        border: 2px solid #ffdb4d;
    }

    .button2_view_classic.button2_theme_flat.button2_checked_yes.button2_pressed_yes:before {
        background-color: transparent;
    }

    .button2_view_classic.button2_theme_flat.button2_disabled_yes:before {
        background-color: var(--yc-lego-color-medium-light-disabled);
    }

    .button2_view_classic.button2_theme_flat.button2_disabled_yes.button2_checked_yes:before {
        background-color: var(--yc-lego-color-light-disabled);
    }

    .button2_view_classic.button2_theme_flat:before {
        transition: background-color 0.1s ease-out;
    }

    .button2_theme_flat .button2__text,
    .button2_theme_flat .button2__icon {
        transition: opacity 0.1s ease-out;
    }

    .button2_theme_flat.button2_hovered_yes .button2__text {
        color: var(--yc-color-text-primary);
    }

    .button2_theme_flat.button2_tone_grey .button2__text,
    .button2_theme_flat.button2_hovered_yes.button2_tone_grey .button2__text {
        color: var(--yc-lego-color-base);
    }

    .button2_theme_flat.button2_hovered_yes:before {
        background-color: var(--yc-color-hover-area);
    }

    .button2_theme_flat.button2_view_default.button2_tone_default.button2_disabled_yes {
        &,
        & .button2__text,
        & .button2__icon {
            color: var(--yc-color-button-disabled);
        }

        &:before {
            background-color: var(--yc-color-button-background-disabled);
        }
    }
}
