@mixin button2_theme_action {
    .button2_theme_action.button2_view_default.button2_tone_default {
        &.button2_disabled_yes:not(.button2_progress_yes) {
            &:before {
                background-color: var(--yc-color-button-background-disabled);
            }

            & .button2__text,
            & .button2__icon {
                opacity: 1;
                color: var(--yc-color-button-disabled);
            }
        }
    }
}
