.yc-header {
    height: 70px;
    border-bottom: 1px solid var(--yc-color-divider);
    position: relative;
    padding: 0 20px 0 12px;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    background-color: var(--yc-color-base);

    &__menu-section {
        margin-right: 12px;
    }

    &__logo-section {
        display: flex;
        align-items: center;
        margin-right: 55px;
    }

    &__search-section {
        flex-grow: 1;
        flex-shrink: 1;
        margin-right: 30px;
    }

    &__custom-section {
        margin-left: auto;
    }

    &__user-section {
        margin-left: 30px;
    }

}
