.yc-item-selector {
    $_: &;

    --yc-list-margin: 16px;
    --yc-list-height: 196px;

    display: flex;
    width: 100%;
    min-height: 200px;

    &__list {
        flex: 0 0 50%;
        padding-top: 8px;

        &:not(:last-child) {
            border-right: 1px solid var(--yc-color-divider);
        }
    }

    &__list-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 8px;
        padding: 0 var(--yc-list-margin);
    }

    &__list-title {
        font-weight: 500;
    }

    &__item {
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;

        &_active {
            #{$_}__item-select {
                display: block;
            }
        }
    }

    &__item-select {
        display: none;
    }

    &__item-text {
        overflow: hidden;
        text-overflow: ellipsis;
        margin-right: auto;
    }

    &__value-item {
        display: flex;
        align-items: center;
        width: 100%;
        overflow: hidden;

        &_active {
            #{$_}__value-item-remove {
                display: block;
            }
        }
    }

    &__value-item-text {
        flex: 1 1 auto;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    &__value-item-remove {
        margin-left: auto;
        flex: 0 0 auto;
        width: 24px;
        height: 24px;
        padding: 7px;
        cursor: pointer;
        display: none;
    }
}
