@import '../../../styles/mixins.scss';

$smallOffset: 10px;
$defaultIconSize: 28px;
$popupCloudIconSize: 32px;
$popupFolderIconSize: 24px;
$minControlWidth: 120px;

:root {
    --yc-cloud-folder-small-offset: #{$smallOffset};
    --yc-cloud-folder-cloud-icon-size: #{$defaultIconSize};
    --yc-cloud-folder-folder-icon-size: #{$defaultIconSize};
    --yc-cloud-folder-folder-offset: #{$smallOffset + $popupCloudIconSize / 2};
}

.yc-cloud-folder-select {
    $class: &;

    display: flex;
    align-items: center;

    color: var(--yc-color-text-primary);
    cursor: pointer;
    user-select: none;

    &__cloud-icon {
        width: var(--yc-cloud-folder-cloud-icon-size);
        height: var(--yc-cloud-folder-cloud-icon-size);

        line-height: var(--yc-cloud-folder-cloud-icon-size);
        text-align: center;
        font-size: 10px;

        flex-shrink: 0;

        color: var(--yc-color-text-special-primary);
        background-color: var(--yc-color-special-area);

        &_accent {
            background-color: var(--yc-color-control-accent);
        }
    }

    &__folder-icon {
        width: var(--yc-cloud-folder-folder-icon-size);
        height: var(--yc-cloud-folder-folder-icon-size);

        line-height: var(--yc-cloud-folder-folder-icon-size);
        text-align: center;
        font-size: 10px;

        flex-shrink: 0;
    }

    &__current {
        display: flex;
        align-items: center;
        min-width: $minControlWidth;

        &-cloud-name {
            &:not(:only-child) {
                opacity: 0.5;
            }
        }

        &-cloud-accent {
            #{$class}__current-cloud-name {
                opacity: 1;
            }
        }

        #{$class}__cloud-icon {
            margin-right: var(--yc-cloud-folder-small-offset);
        }
    }

    &__cloud-note {
        margin-top: 2px;
        font-size: 12px;

        &_accent {
            color: var(--yc-color-text-accent);
        }
    }

    &__complex-icon {
        position: relative;
        width: var(--yc-cloud-folder-cloud-icon-size);
        height: var(--yc-cloud-folder-cloud-icon-size);
        margin-right: 15px;

        #{$class}__cloud-icon {
            --yc-cloud-folder-cloud-icon-size: 22px;
            position: absolute;
            right: 0;
            bottom: 0;
            transform: translate(50%, 50%);
            margin: 0;

            border: 2px solid var(--yc-color-base);
        }
    }

    &__arrow {
        margin-left: 10px;
    }

    &__popup {
        width: 256px;
        @include max-height(320px);

        display: flex;
        flex-direction: column;

        overflow-y: hidden;
        box-sizing: content-box;

        #{$class}__cloud-icon {
            --yc-cloud-folder-cloud-icon-size: #{$popupCloudIconSize};
            margin-right: var(--yc-cloud-folder-small-offset);
        }

        #{$class}__folder-icon {
            --yc-cloud-folder-folder-icon-size: #{$popupFolderIconSize};
            margin-right: var(--yc-cloud-folder-small-offset);
        }
    }

    &__popup-filter {
        flex: 0 0 28px;

        display: flex;
        align-items: center;
        padding: 16px var(--yc-cloud-folder-small-offset) 12px;
    }

    &__popup-empty {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: var(--yc-cloud-folder-small-offset);

        &-icon {
            margin-bottom: var(--yc-cloud-folder-small-offset);
        }

        &-text {
            text-align: center;
            white-space: pre-wrap;
        }
    }

    &__popup-item {
        @include max-text-width(250px);
        padding: 0 var(--yc-cloud-folder-small-offset);

        display: flex;
        align-items: center;
        height: 40px;

        &_type_folder {
            padding-left: var(--yc-cloud-folder-folder-offset);
        }

        &_type_cloud &_accent {
            background-color: var(--yc-color-control-accent);
        }

        &_current {
            background-color: var(--yc-color-selection-solid);
            font-weight: 500;
        }
    }

    &__popup-name {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        @include max-text-width(170px);
    }

    &__loader {
        display: flex;
        flex: 0 0 100px;
        align-items: center;
        justify-content: center;
    }
}
