/**
 * 1.  padding  margin    -    .
 * 2.       ,  .. -.
 * 3.   -    .
 * 4.     .
 * 5.  FF  MacOS   blur      mousedown.
 *    : http://jsfiddle.net/mishaberezin/892yppts/10/.
 * 6.  -.
 * 7.     iOS  Android.
 * 8.  IE   hidden,    visible,  .
 *      visible, ..      .
 * 9.       .
 * 10.     ,    link.
 */
.button2 {
    display: inline-block; /*6*/
    overflow: visible; /*8*/

    box-sizing: border-box;
    margin: 0; /*1*/
    padding: 0; /*1*/

    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    -moz-user-focus: ignore; /*5*/
    user-select: none; /*2*/
    text-align: center; /*10*/
    white-space: nowrap; /*4*/
    text-decoration: none; /*3*/

    background: transparent; /*9*/
    -webkit-tap-highlight-color: rgba(0,0,0,0); /*7*/

    touch-action: manipulation;
}

/**
 *       ,    disabled.
 *    disabled      IE.
 *        pointer-events:none.
 *
 * NB:
 * -         taborder.
 */
.button2[aria-disabled="true"],
.button2_disabled_yes {
    pointer-events: none;
}

/**
 *  FF 4+      ,   
 *       css- border  background.
 *    ,        .
 * : http://stackoverflow.com/questions/8859908.
 */
.button2::-moz-focus-inner {
    padding: 0;

    border: 0;
}

.button2:before {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    content: '';
}
