.ch-control-pane {
  height: 32px;
  width: 100%;
  background: var(--yc-color-base);
  position: relative;
  z-index: 100000;
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--yc-color-divider);

  & > * {
    margin-right: 15px;
  }

  &__header {
    font-size: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__link {
    margin-right: 10px;
  }

  &__button-icon {
    vertical-align: top;
    height: 28px;
    margin-right: 5px;
  }

  & .button2:before {
    border-radius: initial;
  }
}
