.tooltip_view_classic.tooltip_theme_success.popup2_theme_normal {
    color: #fff;
    border: 0;
}

.tooltip_view_classic.tooltip_theme_success.popup2_theme_normal .tooltip__backdrop {
    opacity: .9;
}

.tooltip_view_classic.tooltip_theme_success.popup2_theme_normal .tooltip__backdrop:before {
    background: #6cba68;
}

.tooltip_view_classic.tooltip_theme_success.popup2_theme_normal:before {
    background: transparent;
}

.tooltip_view_classic.tooltip_theme_success .tooltip__backdrop:before {
    box-shadow: none;
}

.tooltip_view_classic.tooltip_theme_success.popup2_theme_normal .popup2__tail:after {
    background-image: linear-gradient(to top right, #6cba68 50%, transparent 50%);
}
