@import '../../../styles/mixins.scss';

.yc-tooltip {
    cursor: pointer;
    display: inline-block;
    position: relative;

    $class: &;

    &__buttons {
        display: inline-flex;
        width: 100%;
        $buttonClass: #{$class}__tooltip-button;

        #{$buttonClass}:first-child {
            margin-right: 10px;
        }
    }

    &__tooltip {
        min-height: 40px;
        max-width: 300px;
        padding: 16px;
        display: flex;
        flex-direction: column;
        cursor: default;

        .tooltip__content {
            text-align: left;
        }

        &-title {
            display: inline-flex;
            margin: 0 0 12px;
            font-size: var(--yc-text-lead-font-size);
            line-height: var(--yc-text-lead-line-height);
            font-weight: 500;
        }

        &-button {
            margin-top: 20px;
            flex: 1;
        }

        &-content {
            font-size: var(--yc-text-body-font-size);
            line-height: var(--yc-text-body-line-height);

            &_tone_secondary {
                opacity: 0.7;
            }

            &_theme_info {
                & a {
                    text-decoration: none;
                    color: var(--yc-color-text-link);

                    &:hover {
                        color: var(--yc-color-text-accent);
                    }
                }
            }

            &_theme_special {
                & a {
                    font-weight: bold;
                    color: var(--yc-color-text-special-primary);

                    &:hover {
                        color: var(--yc-color-text-accent);
                    }
                }
            }
        }

        &-links > * {
            margin-top: 8px;

            &:first-child {
                margin-top: 12px;
            }
        }

        &-link {
            display: inline-block;
            font-size: var(--yc-text-body-font-size);
            line-height: var(--yc-text-body-line-height);
        }
    }

    & .yc-icon {
        vertical-align: middle;
    }
}
