.yc-drop-down-menu {
    &__menu-item_separator {
        border-top: 1px solid var(--yc-color-contrast-7-solid);
        margin: 0.5em 0;
        pointer-events: none;
    }

    &__item {
        display: flex;
        align-items: center;
        cursor: pointer;
    }

    &__icon {
        display: flex;
        margin-right: 4px;
    }

    &__switcher-wrapper {
        display: inline-block;
    }

    &__switcher-button {
        display: flex;
    }
}
