@mixin button2_theme_raised {
    .button2_theme_raised.button2_view_default.button2_tone_default {
        &.button2_disabled_yes {
            &:before {
                box-shadow: none;
                background-color: var(--yc-color-button-background-disabled);
            }

            & .button2__text,
            & .button2__icon {
                opacity: 1;
                color: var(--yc-color-button-disabled);
            }
        }
    }
}
