.query-result {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  position: relative;

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 20px;

    & .yc-tabs {
      display: flex;
      align-items: flex-start;
      font-size: 15px;
      line-height: 20px;
      opacity: 0.85;
      font-weight: 400;

      &__tab {
        min-height: 56px;
        padding-top: 10px;

        &_active {
          border-width: 3px;
          border-color: var(--action-color);
        }

        &:first-child {
          margin-left: 20px;
        }
      }
    }

  }

  &__cell {
    cursor: pointer;
    overflow: hidden;
    max-width: 300px;
    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .data-table {
    width: 100%;

    &__th {
      background: var(--table-header-background);
      color: var(--grey-color-text);
    }

    &__table {
      width: 100%;
      font-size: 13px;
      line-height: 18px;
      border-collapse: separate;
      border-spacing: 0;
    }

    &__th, &__td {
      border: none;
      vertical-align: middle;
    }

    &__box {
      .data-table__table-wrapper {
        padding-bottom: 20px;
      }
    }

    $cell-border: 1px solid var(--data-table-border-color);
    &__row, &__sticky th {
      height: 40px;
      border-bottom: $cell-border;
    }

    &__td {
      border-top: $cell-border;
    }
  }

  &__content {
    overflow: auto;
  }


  &__loader-container {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 100%;
  }

  &__close-icon {
    cursor: pointer;
  }
}
