.user-pic {
    position: relative;

    display: inline-block;

    width: 42px;
    height: 42px;
}

.user-pic_has-plus_yes:after {
    position: absolute;
    z-index: 0;
    top: 50%;
    left: 50%;

    width: 52px;
    height: 52px;
    margin: -26px 0 0 -26px;

    content: '';

    background: url(./user-pic.assets/plus-circle.svg) center center no-repeat;
    background-size: contain;
}

.user-pic__image {
    width: 100%;
    height: 100%;

    border-radius: 100px;
    background-color: #fff;

    object-fit: cover;
}
