.textarea_view_default.textarea_tone_default.textarea_theme_normal .textarea__control {
    color: #000;
}
.textarea_view_default.textarea_tone_default.textarea_theme_normal .textarea__icon {
    color: #ccc;
}
.textarea_view_default.textarea_tone_default.textarea_theme_normal .textarea__clear.textarea__clear_hovered_yes,
.textarea_view_default.textarea_tone_default.textarea_theme_normal .textarea__clear.textarea__clear:hover {
    color: #000;
}
.textarea_view_default.textarea_tone_default.textarea_theme_normal .textarea__box {
    border-color: #d9d9d9;
    background-color: #fff;
}
.textarea_view_default.textarea_tone_default.textarea_theme_normal.textarea_hovered_yes .textarea__box {
    border-color: #cecece;
    background-color: #fff;
}
.textarea_view_default.textarea_tone_default.textarea_theme_normal.textarea_focused_yes .textarea__box{
    border-color: #b3b3b3;
}
.textarea_view_default.textarea_tone_default.textarea_theme_normal .textarea__control::placeholder {
    color: #ccc;
}
.textarea_view_default.textarea_tone_default.textarea_disabled_yes.textarea_theme_normal .textarea__control {
    color: #7f7f7f;
}
.textarea.textarea_view_default.textarea_tone_default.textarea_theme_normal.textarea_disabled_yes .textarea__box {
    border-color: initial;
    background-color: #f2f2f2;
}
.textarea_view_default.textarea_tone_default.textarea_theme_normal.textarea_disabled_yes .textarea__control::placeholder {
    color: #bfbfbf;
}
