@mixin button2_theme_accent {
    .button2_theme_accent {
        position: relative;
        z-index: 0;

        border: none;
        outline: none;
    }

    .button2_theme_accent.button2_view_default.button2_tone_default {
        &:before {
            background-color: var(--yc-color-button-accent-background);
        }

        & .button2__text,
        & .button2__icon {
            color: var(--yc-color-button-accent);
        }

        &.button2_hovered_yes {
            &:before {
                background-color: var(--yc-color-button-accent-background-hover);
            }

            & .button2__text,
            & .button2__icon {
                color: var(--yc-color-button-accent);
            }
        }

        &.button2_disabled_yes {
            &:before {
                background-color: var(--yc-color-button-background-disabled);
            }

            & .button2__text,
            & .button2__icon {
                color: var(--yc-color-button-disabled);
            }
        }

        &.button2_progress_yes {
            &:before {
                background-size: 34px 34px;
                background-color: var(--yc-color-button-accent-background-progress);
                background-image: repeating-linear-gradient(-45deg, var(--yc-color-button-accent-background-progress), var(--yc-color-button-accent-background-progress) 4px, var(--yc-color-button-accent-background-2-progress) 4px, var(--yc-color-button-accent-background-2-progress) 8px);

                animation-name: button2-progress;
                animation-duration: 0.5s;
                animation-timing-function: linear;
                animation-iteration-count: infinite;
            }

            & .button2__text {
                color: var(--yc-color-button-accent-progress);
            }
        }
    }
}
