@mixin lego-theme-dark {
    --yc-lego-color-utilityfocus-solid: var(--yc-color-contrast-35-solid);
    --yc-lego-color-text-primary: var(--yc-color-text-primary);
    --yc-lego-color-text-secondary: var(--yc-color-text-secondary);
    --yc-lego-color-placeholder: var(--yc-color-contrast-35-solid);
    --yc-lego-color-base: var(--yc-color-base);
    --yc-lego-color-modal-veil: var(--yc-color-veil);
    --yc-lego-color-popup-shadow: var(--yc-color-shadow);
    --yc-lego-color-tooltip-shadow: var(--yc-color-shadow);
    --yc-lego-color-tooltip-success-bg: var(--yc-color-apple-solid);
    --yc-lego-color-tooltip-normal-bg: var(--yc-color-contrast-70-solid);
    --yc-lego-color-input-control: var(--yc-color-contrast-17-solid);
    --yc-lego-color-input-disabled: var(--yc-color-contrast-17-solid);
    --yc-lego-color-popup-border: var(--yc-color-divider);
    --yc-lego-color-menu-background: var(--yc-color-float-area);
    --yc-lego-color-border-medium: var(--yc-color-contrast-15);
    --yc-lego-color-border-medium-solid: var(--yc-color-contrast-15);
    --yc-lego-color-border-medium-light: var(--yc-color-contrast-35);
    --yc-lego-color-border-dark: var(--yc-color-contrast-55);
    --yc-lego-color-link: var(--yc-color-link);
    --yc-lego-color-link-disabled: var(--yc-color-contrast-10);
    --yc-lego-color-link-hover: var(--yc-color-link-hover);
    --yc-lego-color-light-disabled: var(--yc-color-contrast-2);
    --yc-lego-color-medium-disabled-solid: var(--yc-color-contrast-4-solid);
    --yc-lego-color-medium-light-disabled: var(--yc-color-contrast-7);
    --yc-lego-color-dark-disabled: var(--yc-color-contrast-35);
    --yc-lego-color-light-hover: var(--yc-color-contrast-2);
    --yc-lego-color-light-hover-solid: var(--yc-color-contrast-2-solid);
    --yc-lego-color-medium-light-hover: var(--yc-color-contrast-4);
    --yc-lego-color-medium-light-hover-solid: var(--yc-color-contrast-4-solid);
    --yc-lego-color-medium-hover-solid: var(--yc-color-hover-area);
    --yc-lego-color-dark-light-hover: var(--yc-color-contrast-17);
    --yc-lego-color-dark-light-hover-solid: var(--yc-color-contrast-17-solid);
    --yc-lego-color-dark-hover-solid: var(--yc-color-contrast-55-solid);
    --yc-lego-color-dark-checked-solid: var(--yc-color-contrast-55-solid);
    --yc-lego-color-checkbox-tick: var(--yc-color-contrast-70-solid);
    --yc-lego-color-grayscale-10: var(--yc-color-contrast-10);
}
