%text-overflow {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.yc-nav-table-view {
    $_: &;
    --yc-navigation-item-height: 40px;
    --yc-navigation-bottom-padding: 20px;

    font-size: 13px;
    width: 100%;
    height: 100%;

    &__row {
        display: flex;
        align-items: center;
        padding: 0 var(--yc-navigation-side-padding);

        &:hover {
            cursor: pointer;
            background: var(--yc-color-hover-area);
        }

        &_hovered {
            background: var(--yc-color-hover-area);
        }

        &_highlight {
            background: var(--yc-color-selection);
        }

        &_hidden {
            #{$_}__name {
                color: var(--yc-color-text-utility);
            }
        }

        &_inactive {
            cursor: default;
            pointer-events: none;

            #{$_}__name,
            #{$_}__icon {
                opacity: 0.5;
            }
        }

        &_locked {
            #{$_}__row-btns {
                visibility: hidden;
            }
        }
    }

    &__row_with-parent-folder {
        #{$_}__createdBy {
            flex: 1 0 10%;
        }

        #{$_}__parent-folder {
            flex: 0 0 30%;
        }

        #{$_}__name {
            flex: 3 1 60%;
        }
    }

    &__row_mode_minimal {
        padding: 0 20px;

        #{$_}__createdBy {
            display: none;
        }

        #{$_}__info {
            padding-right: 0;
        }

        #{$_}__row-btns {
            display: none;
        }

        #{$_}__date {
            flex: 0 0 60px;
            min-width: auto;
        }
    }

    &__row_mode_modal {
        #{$_}__date {
            flex: 0 0 70px;
            min-width: auto;
        }
    }

    &__row-loader-more {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
    }

    &__icon {
        margin-right: 12px;
        position: relative;
        bottom: 2px;
        flex: 0 0 auto;
    }

    $rowBtnsWidth: 86px;

    &__info {
        width: 100%;
        display: flex;
        align-items: center;
        min-height: var(--yc-navigation-item-height);
        position: relative;
        padding-right: $rowBtnsWidth;
        overflow: hidden;
    }

    &__row-btns {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        width: $rowBtnsWidth;
        padding-left: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__row-btn {
        visibility: hidden;
        width: 32px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        border-radius: 4px;

        #{$_}__row:hover &,
        #{$_}__row_hovered & {
            visibility: visible;
        }

        &:hover,
        &_hovered {
            background: var(--yc-color-hover-area);
        }
    }

    &__btn-change-favorite {
        visibility: hidden;
        color: var(--yc-color-text-hint);

        &_isFavorite {
            visibility: visible;
        }

        #{$_}__row:hover &,
        #{$_}__row_hovered & {
            color: #ffb54d;

            &:not(#{$_}__btn-change-favorite_isFavorite) {
                #{$_}__icon-star-stroke {
                    display: block;
                }

                #{$_}__icon-star-fill {
                    display: none;
                }
            }
        }
    }

    &__icon-star-stroke {
        display: none;
    }

    &__lock {
        margin: 0 10px;
        flex: 0 0 16px;
        color: var(--yc-color-text-hint);
    }

    &__name {
        flex: 3 1 30%;
        display: flex;
        overflow: hidden;
        min-width: 100px;
        color: var(--yc-color-text-primary);

        span,
        a {
            @extend %text-overflow;
        }
    }

    &__createdBy {
        flex: 1 0 20%;
        color: var(--yc-color-text-primary);
        display: flex;
        opacity: 0.5;
        min-width: 100px;
        overflow: hidden;
        padding: 0 10px;

        > span {
            @extend %text-overflow;
        }
    }

    &__date {
        flex: 0 0 140px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        color: var(--yc-color-text-primary);
        opacity: 0.5;
        min-width: 100px;
        overflow: hidden;

        > span {
            @extend %text-overflow;
        }
    }

    &__link {
        &,
        &:hover,
        &:active,
        &:visited,
        &:focus {
            text-decoration: none;
            outline: none;
            color: inherit;
        }

        &_inactive {
            &,
            &:active,
            &:focus {
                pointer-events: none;
            }
        }
    }

    &__list {
        margin-bottom: var(--yc-navigation-bottom-padding);
    }

    &__name-line {
        display: flex;
        flex: 1 1 auto;
        line-height: 20px;
        overflow: hidden;
    }

    &__parent-folder {
        height: 20px;
        display: flex;
        align-items: center;
        flex: 0 0 50%;
        font-size: 13px;
        overflow: hidden;
    }

    &__folder-inline.yc-icon {
        flex: 0 0 16px;
        color: var(--yc-color-text-hint);
        margin-right: 2px;
    }

    &__parent-folder-name {
        opacity: 0.5;

        &:hover {
            opacity: 0.7;
        }
    }
}
