$SEARCH_HEIGHT: 37px;
$SELECTED_TITLE_HEIGHT: 32px;
$APPLY_BUTTON_HEIGHT: 38px;

.yc-select-items {
    max-height: 50vh;

    &_with-search {
        max-height: calc(50vh - #{$SEARCH_HEIGHT});
    }

    &_with-selected-title {
        max-height: calc(50vh - #{$SELECTED_TITLE_HEIGHT});
    }

    &_with-apply-button {
        max-height: calc(50vh - #{$APPLY_BUTTON_HEIGHT});
    }

    &_with-search#{&}_with-apply-button {
        max-height: calc(50vh - #{$SEARCH_HEIGHT} - #{$APPLY_BUTTON_HEIGHT});
    }

    &_with-selected-title#{&}_with-apply-button {
        max-height: calc(50vh - #{$SELECTED_TITLE_HEIGHT} - #{$APPLY_BUTTON_HEIGHT});
    }

    &_without-virtualized {
        overflow-y: auto;

        & .yc-select-item {
            margin-top: 0;
        }

        & .yc-select-item:first-child {
            margin-top: 4px;
        }
    }

    &__group-title {
        padding: 5px 0 0 12px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        color: var(--yc-color-text-hint);

        &:first-child {
            padding-top: 5px;
        }

        &:not(:first-child) {
            border-top: 1px solid var(--yc-color-divider);
        }
    }

    &__group-title ~ .yc-select-item {
        margin-top: 0;
    }

    &__prompt {
        padding: 10px 16px;
        color: var(--yc-color-text-secondary);
    }

    &__prompt-button {
        display: flex;
        margin: 8px 0 2px;
    }

    &__show-more-link {
        display: flex;
        align-items: center;
        padding-left: 12px;
    }

    &__loader {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 8px 0;
    }
}
