/**
 * 1.    mouseover/mouseout  .
 * 2.  IE9+   position:relative  <button>    "".
 *    : http://stackoverflow.com/questions/6744406.
 * 3.  ,   position: relative  icon_type_glyph.
 */
.button2 .button2__icon {
    position: relative; /*2*/

    pointer-events: none; /*1*/
}

.button2 .button2__icon_side_left {
    position: absolute;
    top: 0;
    left: 0;
}

.button2 .button2__icon_side_right {
    position: absolute;
    top: 0;
    right: 0;
}
