
@mixin overflow-ellipsis($type: ellipsis) {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: $type;
}

@mixin button-reset() {
    margin: 0;
    padding: 0;

    font-size: inherit;
    font-weight: 400;

    color: inherit;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
}

@mixin expand-interactive-area($hSize: 4px, $vSize: $hSize) {
    position: relative;

    &::after {
        content: '';
        position: absolute;
        top: -$vSize;
        left: -$hSize;
        right: -$hSize;
        bottom: -$vSize;
    }
}

@mixin max-height($height) {
    max-height: $height;
    overflow-y: auto;
}

@mixin max-text-width($width: 100%) {
    @include overflow-ellipsis();

    max-width: $width;
}

@mixin islands-focus() {
    .utilityfocus &:focus {
        outline: 2px solid #fc0;
    }
}

@mixin aspect-ratio($width, $height) {
    position: relative;

    &::before {
        display: block;
        content: '';
        width: 100%;
        padding-top: ($height / $width) * 100%;
    }

    > *:only-child {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}

@mixin stretch() {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}

@mixin substrate($color: var(--yc-color-base)) {
    &::before {
        content: '';
        @include stretch();
        background-color: $color;
        z-index: -1;
    }
}
