.yc-path-select {
    display: flex;

    &__button-content {
        display: flex;
        align-items: center;
    }

    &__path-text {
        flex: 1 1 auto;
        margin: 0 8px;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    &__button-place {
        max-width: 50%;

        &_withInput {
            margin-right: 8px;
        }
    }

    &__button {
        .button2__text {
            margin: 0 8px;
        }
    }

    & .yc-icon {
        flex-shrink: 0;
    }
}
