.yc-dialog-btn-close {
    position: absolute;
    top: calc(var(--yc-dialog-header-padding-top) - 6px);
    right: calc(var(--yc-dialog-side-padding) - 12px);
    z-index: 1;

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 36px;
    }
}
