.menu {
    display: inline-block;
    overflow-x: hidden;
    overflow-y: auto;

    box-sizing: border-box;

    user-select: none;

    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.menu__item {
    white-space: nowrap;
}

.menu_type_navigation {
    margin: 0;
    padding: 0;

    list-style: none;
}

.menu_type_navigation .menu__group {
    margin: 0;

    list-style: none;
}

/**
 *       ,    disabled.
 *
 * -       taborder.
 */
.menu_disabled_yes,
.menu__item_disabled_yes {
    cursor: default;
    pointer-events: none;
}

/**
 * FF  padding-bottom    overfolow:auto,
 *        -.
 * : https://bugzilla.mozilla.org/show_bug.cgi?id=748518
 */
.menu:before,
.menu:after {
    display: block;

    content: '';
}
