.select2_baseline_yes.select2 {
    display: inline-flex;
    vertical-align: baseline;
}

.select2_baseline_yes .button2_width_auto .button2__text {
    /*https://st.yandex-team.ru/ISL-5076
      firefox -    overflow: hidden,     baseline*/
    overflow: -moz-hidden-unscrollable;
}

.select2_baseline_yes .button2_width_max .button2__text {
    /*https://st.yandex-team.ru/ISL-5076
      firefox -    overflow: hidden,     baseline*/
    overflow: -moz-hidden-unscrollable;
}

.select2_baseline_yes.select2_width_fixed .select2__popup {
    margin: -3px 0 0;
}

.select2_baseline_yes.select2_width_fixed .select2__popup .menu:before {
    height: 0;
}

.select2_baseline_yes.select2_width_fixed .select2__popup .menu__item {
    height: 0;
}

.select2_baseline_yes.select2_width_fixed .select2__popup .menu:after {
    height: 0;
}

.select2_baseline_yes.select2_width_max {
    display: block;
}
