@mixin icon_svg_yes {
    .icon_svg_yes {
        &::before,
        &::after {
            content: none !important; // stylelint-disable declaration-no-important
        }
    }

    .icon_svg_yes.button2__icon {
        display: flex;
        align-items: center;
        justify-content: center;

        height: 100%;
    }
}
