.ch-sidebar {
    display: flex;
    flex: 1;
    height: calc(100vh - 56px);
    min-width: 304px;
    background: var(--dark-grey);
    border-right: 1px solid var(--yc-color-divider);

    &__menu {
        width: 100%;
        padding: 16px 0;
    }

    &__menu-group-title {
        color: var(--white-color-text);
        padding: 0 15px;

        cursor: pointer;
    }

    &__menu-item {
        padding: 0 30px;

        cursor: pointer;
        color: var(--disable-color-text);

        &_main {
            font-size: var(--yc-text-body2-font-size);
            line-height: var(--yc-text-body2-line-height);
            padding: 0 15px;
        }
    }
    &__menu-group-title, &__menu-item {
        font-size: 16px;
        line-height: 19px;
        height: 48px;
        display: flex;
        align-items: center;

        &:hover {
            color: var(--white-color-text);
            background: var(--dark-grey-2);
        }

        &_selected {
            color: var(--white-color-text);
            background: var(--dark-grey-2);
        }

        &_highlighted {
            color: var(--white-color-text);
        }
    }

}
