.yc-scrollable-list-with-sticky {
    flex-grow: 1;

    overflow-y: auto;
    height: 400px;

    &__item {
        cursor: pointer;

        &_sticky {
            position: sticky;
            top: 0;
            background-color: var(--yc-color-float-area);
            z-index: 1;
        }

        &_inactive {
            cursor: default;
        }

        &_selected {
            background-color: var(--yc-color-hover-area-solid);
        }
    }
}
