.textinput_view_default.textinput_tone_market.textinput_theme_normal .textinput__control {
    color: #000;
}
.textinput_view_default.textinput_tone_market.textinput_theme_normal .textinput__icon {
    color: #ccc;
}
.textinput_view_default.textinput_tone_market.textinput_theme_normal .textinput__clear.textinput__clear_hovered_yes,
.textinput_view_default.textinput_tone_market.textinput_theme_normal .textinput__clear.textinput__clear:hover {
    color: #000;
}
.textinput_view_default.textinput_tone_market.textinput_theme_normal .textinput__box {
    border-color: #d9d9d9;
    background-color: #fff;
}
.textinput_view_default.textinput_tone_market.textinput_theme_normal.textinput_hovered_yes .textinput__box {
    border-color: #cecece;
    background-color: #fff;
}
.textinput_view_default.textinput_tone_market.textinput_theme_normal.textinput_focused_yes .textinput__box{
    border-color: #b3b3b3;
}
.textinput_view_default.textinput_tone_market.textinput_theme_normal .textinput__control::placeholder {
    color: #ccc;
}
.textinput_view_default.textinput_tone_market.textinput_disabled_yes.textinput_theme_normal .textinput__control {
    color: #7f7f7f;
}
.textinput.textinput_view_default.textinput_tone_market.textinput_theme_normal.textinput_disabled_yes .textinput__box {
    border-color: transparent;
    background-color: #f2f2f2;
}
.textinput_view_default.textinput_tone_market.textinput_theme_normal.textinput_disabled_yes .textinput__control::placeholder {
    color: #bfbfbf;
}
