.yc-share-list {
    display: flex;
    align-items: center;

    &__social {
        display: flex;
        align-items: center;
    }

    &__separator {
        align-self: stretch;
        width: 1px;
        margin: -2px 8px;

        background: var(--yc-color-divider);
    }

    &__link + &__link {
        margin-left: 4px;
    }

    @media (max-width: 639px) {
        flex-direction: column;
        align-items: stretch;
        min-width: 175px;

        &__social {
            justify-content: space-evenly;
        }

        &__separator {
            width: calc(100% + 4px);
            height: 1px;
            margin: 8px -2px;
        }
    }
}
