$active-color: var(--yc-color-loader-active);
$inactive-color: var(--yc-color-loader-inactive);
$pulse-duration: 800ms;
$pulse-delay: $pulse-duration / 4;
$animation: yc-pulse ease $pulse-duration infinite;

$base-height-s: 20px;
$base-height-m: 28px;
$base-height-l: 36px;

@keyframes yc-pulse {
    50% {
        background: $active-color;
    }
}

@mixin loaderSize($height) {
    width: $height / 4;
    height: $height;
}

@mixin loaderPseudoSize($height) {
    height: ($height / 1.5);
    width: ($height / 4);
}

.yc-loader {
    position: relative;
    background: $inactive-color;
    animation: $animation;
    animation-delay: 2 * $pulse-delay;
    &:before, &:after {
        content: '';
        position: absolute;
        display: block;
        background: $inactive-color;
        top: 50%;
        transform: translateY(-50%);
        animation: $animation;
    }

    &:before {
        animation-delay: $pulse-delay;
    }

    &:after {
        animation-delay: 3 * $pulse-delay;
    }

    &_size_s {
        @include loaderSize($base-height-s);

        &:before {
            @include loaderPseudoSize($base-height-s);
            left: -($base-height-s / 2);
        }

        &:after {
            @include loaderPseudoSize($base-height-s);
            left: ($base-height-s / 2);
        }
    }

    &_size_m {
        @include loaderSize($base-height-m);

        &:before {
            @include loaderPseudoSize($base-height-m);
            left: -($base-height-m / 2);
        }

        &:after {
            @include loaderPseudoSize($base-height-m);
            left: ($base-height-m / 2);
        }
    }

    &_size_l {
        @include loaderSize($base-height-l);

        &:before {
            @include loaderPseudoSize($base-height-l);
            left: -($base-height-l / 2);
        }

        &:after {
            @include loaderPseudoSize($base-height-l);
            left: ($base-height-l / 2);
        }
    }
}
