@mixin button2_theme_link {

    .button2_theme_link.button2_view_default {
        color: var(--yc-color-button-link-text);

        &:before {
            border-color: var(--yc-color-button-link-border);
        }
    }

    .button2_theme_link.button2_view_default.button2_tone_default {
        &.button2_disabled_yes {
            &:before {
                background-color: var(--yc-color-button-background-disabled);
            }

            & .button2__text {
                opacity: 1;
                color: var(--yc-color-button-disabled);
            }

            & .button2__icon {
                color: var(--yc-color-button-disabled);
            }
        }
    }
}
