@mixin button2_theme_pseudo-special {
    .button2_theme_pseudo-special {
        position: relative;
        z-index: 0;

        border: none;
        outline: none;
    }

    .button2_theme_pseudo-special.button2_view_default.button2_tone_default {
        &:before {
            border: 1px solid var(--yc-color-button-pseudo-special-border);
        }

        .button2__text,
        .button2__icon {
            color: var(--yc-color-button-pseudo-special);
        }

        &.button2_hovered_yes {
            &:before {
                border: none;
                background-color: var(--yc-color-button-pseudo-special-background-hover);
            }

            .button2__text,
            .button2__icon {
                color: var(--yc-color-button-pseudo-special);
            }
        }

        &.button2_disabled_yes {
            &:before {
                border: none;
                background-color: var(--yc-color-button-special-background-disabled);
            }

            .button2__text,
            .button2__icon {
                color: var(--yc-color-button-special-disabled);
            }
        }
    }
}
