.yc-social-share-link {
    display: flex;
    align-items: center;

    &__icon_type {
        &_telegram {
            color: var(--yc-color-telegram);
        }

        &_facebook {
            color: var(--yc-color-facebook);
        }

        &_twitter {
            color: var(--yc-color-twitter);
        }

        &_vk {
            color: var(--yc-color-vk);
        }
    }
}
