.user-hat,
.user-hat__remote {
    position: relative;
    z-index: 0;

    display: block;

    height: 112px;

    text-decoration: none;

    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

/*    ,   svg. */

.user-hat.user-hat_hidden_yes {
    display: none;
}

.user-hat {
    overflow: hidden;
    /* https://st.yandex-team.ru/BSDEV-70467#1530025292000 */

    width: 280px;

    cursor: pointer;
}

.user-hat__remote {
    box-sizing: border-box;
    padding: 13px 0 15px 13px;
}

.user-hat__bg,
.user-hat__fg {
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;

    width: 280px;
    height: 112px;

    background-repeat: no-repeat;
    background-position: center right;
    background-size: cover;
}

/**
 * 1. -webkit-transform   ,
 * ..  Safari,   ie11.
 **/
.user-hat__fg {
    /*       */
    -webkit-transform: translateZ(0); /* 1 */
}

.user-hat__bg {
    transition: transform .3s ease-in-out;
}

/* TODO:    touch -  ? */
.user-hat .user-hat__remote:active .user-hat__bg {
    transform: scale(1.3);
}


.user-hat__text,
.user-hat__title {
    color: #fff;

    -webkit-transform: translateZ(0); /* 1 */
}

.user-hat__text {
    width: 168px;

    font-size: 13px;
    font-weight: 400;
    line-height: 16px;
}

.user-hat__title {
    overflow: hidden;

    width: 180px;
    padding-bottom: 4px;

    font-size: 16px;
    font-weight: 700;
    line-height: 16px;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.user-hat .user-hat__close-button svg * {
    fill: #fff;
}

.user-hat .user-hat__close-button {
    position: absolute;
    top: 0;
    right: 0;

    display: inline-block;

    box-sizing: border-box;
    width: 26px;
    height: 26px;
    padding: 5px 5px 1px 1px;
}

.user-hat .user-hat__close-button .icon {
    width: 20px;
    height: 20px;
}

.user-hat .user-hat__close-button .icon:before {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    content: '';

    opacity: 0;
    border-radius: 100px;
    background-color: #fff;

    transition: opacity .2s ease-in-out;
}
