.textinput__control {
    position: relative;
    z-index: 2; /*   __box,     */

    box-sizing: border-box;
    width: 100%;
    margin: 0;
    padding: 0;

    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 100%;
    vertical-align: top;

    border: 0 solid transparent;
    outline: 0;
    background: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.textinput_disabled_yes .textinput__control {
    z-index: 3;

    cursor: default;
    user-select: none;
}

/*     */
.textinput__control::-webkit-search-decoration {
    appearance: none;
}

/*   */
.textinput__control::-webkit-search-cancel-button {
    display: none;
}

.textinput__control::-ms-clear {
    display: none;
}

/*   background     */
/* ,    border  _size_ */
.textinput__control.textinput__control:-webkit-autofill {
    border-top-width: 2px;
    border-bottom-width: 2px;
    background-clip: content-box;
    box-shadow: inset 0 0 0 50px #fff;
    -webkit-text-fill-color: #000;
}

.textinput__control::placeholder {
    opacity: 1; /* firefox   */
}
