.tooltip__backdrop {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.tooltip__backdrop:before {
    display: block;

    width: 100%;
    height: 100%;

    content: '';

    border-radius: .1px;

    background: #fff;
    box-shadow: 0 10px 20px -5px rgba(0,0,0,.4);
}

/* NOTE: ISL-5672 */
.tooltip_shown_yes .tooltip__backdrop {
    z-index: -1;
}
