.tooltip_view_classic.tooltip_theme_normal.popup2_theme_normal {
    color: #fff;
    border: 0;
}

.tooltip_view_classic.tooltip_theme_normal.popup2_theme_normal .tooltip__backdrop {
    opacity: .8;
}

.tooltip_view_classic.tooltip_theme_normal.popup2_theme_normal .tooltip__backdrop:before {
    background: #323232;
}

.tooltip_view_classic.tooltip_theme_normal.popup2_theme_normal:before {
    background: transparent;
}

.tooltip_view_classic.tooltip_theme_normal .tooltip__backdrop:before {
    box-shadow: none;
}

.tooltip_view_classic.tooltip_theme_normal.popup2_theme_normal .popup2__tail:after {
    background-image: linear-gradient(to top right, #323232 50%, transparent 50%);
}
