.yc-root .staff-card {
    &__popup, &__input, &__link, &__button {
        color: var(--yc-color-text-primary);
    }

    &__input, &__button {
        border: 1px solid var(--yc-color-divider);
    }

    &__input, &__button:active {
        background: var(--yc-color-base-area);
    }

    &__link:hover {
        color: var(--yc-color-link-hover);
    }

    &__popup {
        background: var(--yc-color-float-area);
    }
}
