$pin-color: var(--yc-color-special-area);
$pin-focus-color: var(--yc-color-selection);
$pin-disabled-color: var(--yc-color-veil);

.yc-range-input-picker {
    outline: none;
    position: relative;

    &__range-info-items-list {
        position: absolute;
        top: 44px;
        left: 0;
        width: 100%;
        display: flex;
        justify-content: space-between;
    }

    &__range-info-item-container {
        position: absolute;
        display: inline-block;
        vertical-align: middle;
        text-align: center;
    }

    &__range-info-item {
        cursor: pointer;
    }

    .rc-slider {
        position: relative;
        bottom: 6px;
        z-index: 3;
        height: 14px;
        padding: 5px 0;
        width: 100%;
        border-radius: 6px;
        touch-action: none;
        box-sizing: border-box;

        &-rail {
            position: absolute;
            width: 100%;
            height: 4px;
            border-radius: 6px;
        }

        &-track {
            position: absolute;
            left: 0;
            transform: translateY(-50%);
            height: 4px;
            border-radius: 6px;
            background-color: $pin-color;
            transition: all 50ms ease;
        }

        &-handle {
            position: absolute;
            top: -1px;
            width: 13px;
            height: 13px;
            cursor: pointer;
            border-radius: 50%;
            border: solid 2px $pin-color;
            background-color: $pin-color;
            touch-action: pan-x;
            transition: all 50ms ease;

            &:hover {
                border-color: $pin-color;
            }

            &:active {
                border-color: $pin-color;
                box-shadow: 0 0 5px $pin-color;
                cursor: grabbing;
            }

            &:focus {
                border-color: $pin-color;
                box-shadow: 0 0 0 5px $pin-focus-color;
                outline: none;
            }
        }

        &-disabled .rc-slider-handle {
            border-color: var(--yc-color-base);
            box-shadow: none;
            background-color: var(--yc-color-float-area);
            cursor: not-allowed;
        }

        &-disabled .rc-slider-track {
            background-color: $pin-disabled-color;
        }

        &-step {
            position: absolute;
            transform: translateY(-50%);
            width: 100%;
            height: 4px;
            background: transparent;
        }

        &-mark {
            position: absolute;
            top: 18px;
            left: 0;
            width: 100%;
            display: flex;
            justify-content: space-between;

            &-text {
                //    8.7.1 rc-slider  - 
                //      transform: translateX(-50%)
                //stylelint-disable-next-line
                transform: translateX(0) !important;
                cursor: pointer;
            }
        }
    }

    &_size_s {
        .rc-slider {
            &-handle {
                width: 11px;
                height: 11px;
            }

            &-track {
                height: 3px;
            }

            &-step {
                height: 3px;
            }
        }
    }

    &_size_xs {
        .rc-slider {
            &-handle {
                width: 9px;
                height: 9px;
            }

            &-track {
                height: 2px;
            }

            &-step {
                height: 2px;
            }
        }
    }

}
