.tooltip_view_classic.tooltip_theme_error.popup2_theme_normal {
    color: #fff;
    border: 0;
}

.tooltip_view_classic.tooltip_theme_error.popup2_theme_normal .tooltip__backdrop {
    opacity: .9;
}

.tooltip_view_classic.tooltip_theme_error.popup2_theme_normal .tooltip__backdrop:before {
    background: #ff6464;
}

.tooltip_view_classic.tooltip_theme_error.popup2_theme_normal:before {
    background: transparent;
}

.tooltip_view_classic.tooltip_theme_error .tooltip__backdrop:before {
    box-shadow: none;
}

.tooltip_view_classic.tooltip_theme_error.popup2_theme_normal .popup2__tail:after {
    background-image: linear-gradient(to top right, #ff6464 50%, transparent 50%);
}
