.yc-select-search {
    display: flex;
    align-items: center;
    padding: 0 3px 0 8px;
    width: 100%;
    height: 37px;
    border-bottom: 1px solid var(--yc-color-divider);

    &__input-wrap {
        flex: 1 1 auto;
    }

    &__select-all-button {
        margin-right: 2px;
    }

    & .textinput__box {
        border: none;
    }
}
