.yc-select-control {
    box-sizing: border-box;
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 8px;
    border: 1px solid var(--yc-color-divider);
    border-radius: 3px;
    user-select: none;
    transition: border-color 0.1s linear, background-color 0.1s linear;
    cursor: pointer;

    &_size {
        &_xs {
            height: 24px;
        }

        &_s {
            height: 28px;
        }

        &_m {
            height: 32px;
        }

        &_n {
            height: 36px;
        }
    }

    &_focused {
        border-color: var(--yc-color-decoration);
    }

    &_stretched {
        width: 100%;
    }

    &_disabled {
        color: var(--yc-color-text-inactive);
        pointer-events: none;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: var(--yc-color-divider);
        }
    }

    &:hover {
        background-color: var(--yc-color-hover-area);
    }

    &:active {
        transform: scale(0.98);
    }

    &__label {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-right: 6px;
        font-weight: 700;
        white-space: nowrap;
    }

    &__tokens {
        display: flex;
        align-items: center;
        line-height: 26px;
        flex: 1 1 auto;
        white-space: nowrap;
        overflow: hidden;
    }

    &__tokens-text {
        padding-right: 8px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }

    &__placeholder-icon,
    &__selected-item-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 16px;
        height: 16px;
        margin-right: 5px;
    }

    &__badge {
        display: flex;
        align-items: center;
        height: 20px;
        padding: 0 6px;
        background-color: var(--yc-color-selection);
        border-radius: 4px;
        transition: background-color 0.1s linear;

        &_clicked {
            background-color: var(--yc-color-selection-accent);
            color: var(--yc-color-text-special-primary);
        }

        &_without-arrow {
            margin-right: 3px;
        }
    }

    &__arrow {
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 30px;
        height: 100%;
    }
}

.yc-select-action {
    padding: 0 2px 2px;
}

.yc-select-popup {
    max-width: 400px;
    border-radius: 4px;
    background-color: var(--yc-color-base);
    border-color: var(--yc-color-divider);
    overflow: hidden;
    box-shadow: 0 2px 4px 0 var(--yc-color-shadow);

    &_multi {
        min-width: 150px;
    }

    &_search {
        min-width: 170px;
    }

    &_search#{&}_multi {
        min-width: 200px;
    }

    &__select-title {
        padding: 12px 0 4px 12px;
        font-weight: 500;
    }
}
