.yc-datepicker-presets {
    flex: 0 0 40px;
    display: flex;
    align-items: center;
    color: var(--yc-color-contrast-50-solid);
    background-color: var(--yc-color-base-area);

    &__item {
        margin-left: 16px;
        cursor: pointer;

        &:hover {
            color: var(--yc-color-contrast-70-solid);
        }
    }
}
