@import '../../../../styles/mixins.scss';

$headerHeight: 70px;
$zIndexMenu: 1000;
$zIndexBack: 999;
$sideMenuWidth: 256px;
$slideDuration: 0.3s;
$slideBackgroundDuration: 0.15s;
$blindColor: var(--yc-color-veil);

$class: 'yc-header-menu';

.#{$class} {
    &__hamburger-place {
        @include button-reset();
        width: 36px;
        height: 36px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
    }

    &__inner {
        visibility: hidden;
        position: fixed;
        top: $headerHeight;
        left: 0;
        bottom: 0;
        width: $sideMenuWidth;
        overflow-y: auto;
        z-index: $zIndexMenu;
        background: var(--yc-color-base);

        &_view_close {
            transform: translateX(-$sideMenuWidth);
            transition: transform $slideDuration;
        }

        &_view_open {
            transform: translateX(0px);
            transition: transform $slideDuration;
        }
    }

    &__inner-content {
        @include stretch();
    }

    &__blind {
        visibility: hidden;
        position: fixed;
        top: $headerHeight;

        background: transparent;

        z-index: $zIndexBack;
    }

    &__blind_view_open {
        animation: flamingBlind $slideBackgroundDuration linear forwards;
    }

    &__blind_view_close {
        animation: fadingBlind $slideBackgroundDuration linear forwards;
    }
}


@keyframes flamingBlind {
    from {
        width: 100%;
        height: 100%;
        background: transparent;
    }
    to {
        width: 100%;
        height: 100%;
        background: $blindColor;
    }
}

@keyframes fadingBlind {
    from {
        width: 100%;
        height: 100%;
        background: $blindColor;
    }
    99% {
        background: transparent;
        width: 100%;
        height: 100%;
    }
    to {
        width: 0;
        height: 0;
    }
}
