.textarea_view_default.textarea_tone_red.textarea_theme_normal .textarea__control {
    color: #fff;
}
.textarea_view_default.textarea_tone_red.textarea_theme_normal .textarea__icon {
    color: #ee3030;
}
.textarea_view_default.textarea_tone_red.textarea_theme_normal .textarea__clear.textarea__clear_hovered_yes,
.textarea_view_default.textarea_tone_red.textarea_theme_normal .textarea__clear.textarea__clear:hover {
    color: #fff;
}
.textarea_view_default.textarea_tone_red.textarea_theme_normal .textarea__box {
    border-color: transparent;
    background-color: #9e2020;
}
.textarea_view_default.textarea_tone_red.textarea_theme_normal.textarea_hovered_yes .textarea__box {
    border-color: transparent;
    background-color: #961e1e;
}
.textarea_view_default.textarea_tone_red.textarea_theme_normal.textarea_focused_yes .textarea__box{
    border-color: #ff3838;
}
.textarea_view_default.textarea_tone_red.textarea_theme_normal .textarea__control::placeholder {
    color: #ee3030;
}
.textarea_view_default.textarea_tone_red.textarea_disabled_yes.textarea_theme_normal .textarea__control {
    color: #e29393;
}
.textarea.textarea_view_default.textarea_tone_red.textarea_theme_normal.textarea_disabled_yes .textarea__box {
    border-color: initial;
    background-color: #b22424;
}
.textarea_view_default.textarea_tone_red.textarea_theme_normal.textarea_disabled_yes .textarea__control::placeholder {
    color: #e29393;
}
