.yc-datepicker {
    &__control {
        width: 314px;
        min-width: 100px;
    }

    &__popup {
        --yc-tabs-color-border-active: var(--yc-color-control-accent);
        --yc-tabs-space-between: 16px;
        --yc-tabs-border-width: 2px;

        width: 314px;
        height: 50vh;
        overflow: hidden;
        border-radius: 4px;
        border-color: var(--yc-color-divider);
        box-shadow: 0 2px 4px 0 var(--yc-color-shadow);
        user-select: none;
    }
}
