/*   icon_glyph */
.user-account__logout.user-account__logout {
    position: absolute;
    top: 50%;
    right: 13px;

    display: none;

    margin-top: -8px;

    cursor: pointer;

    opacity: .2;
}

.user-account_hovered_yes .icon.user-account__logout {
    display: block;

    transition: opacity .2s ease-out;
}

