.ch-header {
    height: 56px;
    width: 100%;
    background: var(--dark-grey-2);
    position: relative;
    z-index: 100000;
    padding: 0 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid var(--yc-color-divider);

    &__logo-link {
        text-decoration: none;
    }

    &__logo {
        display: flex;
        align-items: center;
    }

    &__logo-title {
        margin: 0 8px;
    }

    &__logo-subtitle {
        font-size: 21px;
        line-height: 25px;
        font-weight: 300;
        color: var(--white-color-text);
    }

    &__space-divider {
        flex: 1;
    }

    &__cloud-link {
        display: flex;
        align-items: center;
        color: var(--white-color-text);
        text-decoration: none;
    }

    &__logo-cloud {
        width: 63px; // 83px for 21px
        height: 21px;
        background-image: url("https://yastatic.net/q/logoaas/Yandex%20Cloud.svg?color=fff&first=fff&size=16");
    }

    &__menu-link {
        display: flex;
        align-items: center;
        color: var(--white-color-text);
        font-weight: 600;
        font-size: 16px;
        line-height: 19px;
        margin-right: 32px;
        text-decoration: none;
    }
}
