.yc-navigation-breadcrumbs {
    --yc-crumb-font-size: 15px;
    --yc-crumb-margin-between: 36px;
    --yc-crumb-padding: 8px 0;
    --yc-crumb-tick-size: 7px;
    --yc-crumb-tick-weight: 1.5px;
    --yc-crumb-tick-position: -18px;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    font-size: var(--yc-crumb-font-size);

    &_size_s {
        --yc-crumb-font-size: 13px;
        --yc-crumb-margin-between: 28px;
        --yc-crumb-padding: 6px 0;
        --yc-crumb-tick-size: 5px;
        --yc-crumb-tick-weight: 0.7px;
        --yc-crumb-tick-position: -14px;
    }

    &__divider-name {
        display: flex;
        align-items: center;
    }

    &__link {
        text-decoration: none;
    }

    &__crumb {
        padding: var(--yc-crumb-padding);
    }

    &__item {
        position: relative;
        cursor: pointer;
        margin-right: var(--yc-crumb-margin-between);
        text-decoration: none;

        color: var(--yc-color-text-inactive);

        ::after {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            right: var(--yc-crumb-tick-position);
            border-color: var(--yc-color-text-inactive);
            border-width: var(--yc-crumb-tick-weight);
            border-style: solid solid none none;
            width: var(--yc-crumb-tick-size);
            height: var(--yc-crumb-tick-size);
            transform: translate(0, -50%) rotate(45deg);
            pointer-events: none;
        }

        &:hover {
            color: var(--yc-color-text-primary);
        }

        &_last {
            cursor: default;
            font-weight: 500;
            color: var(--yc-color-text-primary);

            ::after {
                display: none;
                content: '';
            }
        }
    }
}
