.textarea_view_default.textarea_tone_grey.textarea_theme_normal .textarea__control {
    color: #000;
}
.textarea_view_default.textarea_tone_grey.textarea_theme_normal .textarea__icon {
    color: #c4c4c4;
}
.textarea_view_default.textarea_tone_grey.textarea_theme_normal .textarea__clear.textarea__clear_hovered_yes,
.textarea_view_default.textarea_tone_grey.textarea_theme_normal .textarea__clear.textarea__clear:hover {
    color: #000;
}
.textarea_view_default.textarea_tone_grey.textarea_theme_normal .textarea__box {
    border-color: #d0d0d0;
    background-color: #fff;
}
.textarea_view_default.textarea_tone_grey.textarea_theme_normal.textarea_hovered_yes .textarea__box {
    border-color: #c6c6c6;
    background-color: #fff;
}
.textarea_view_default.textarea_tone_grey.textarea_theme_normal.textarea_focused_yes .textarea__box{
    border-color: #acacac;
}
.textarea_view_default.textarea_tone_grey.textarea_theme_normal .textarea__control::placeholder {
    color: #c4c4c4;
}
.textarea_view_default.textarea_tone_grey.textarea_disabled_yes.textarea_theme_normal .textarea__control {
    color: #7a7a7a;
}
.textarea.textarea_view_default.textarea_tone_grey.textarea_theme_normal.textarea_disabled_yes .textarea__box {
    border-color: initial;
    background-color: #e9e9e9;
}
.textarea_view_default.textarea_tone_grey.textarea_theme_normal.textarea_disabled_yes .textarea__control::placeholder {
    color: #b7b7b7;
}
