.user-pic__camera {
    position: absolute;
    z-index: 1;
    top: 0%;
    overflow: hidden;
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.user-pic__link {
    position: absolute;
    bottom: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    outline: none;
    background-color: rgba(0, 0, 0, .5);
}

.user-pic__camera:after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 24px;
    height: 24px;
    content: '';
    pointer-events: none;
    background-image: url('../user-pic.assets/camera.svg');
    transform: translate(-50%, -50%);
}
