/* general */
.menu.menu_view_default .menu__item {
    position: relative;
}

/**
 *      background, ,    ,
 *   margin  :first-child
 */

.menu.menu.menu_view_default .menu__item .icon_glyph_type-check {
    top: -1px;
    visibility: hidden;

    width: 16px;
    margin-right: 5px;
    margin-left: 0;
}

.menu.menu_view_default .menu__item.menu__item_checked_yes .icon_glyph_type-check {
    visibility: visible;
}

