.yc-entry-title {
    display: inline-flex;
    align-items: center;
    color: var(--yc-color-text-primary);
    overflow: hidden;
    width: 100%;

    &_pointer {
        cursor: pointer;
    }

    &_theme_header {
        font-size: 15px;
    }

    &_theme_title {
        font-size: 17px;
        font-weight: 700;
    }

    &_theme_inline {
        font-size: 15px;
    }

    &__icon {
        line-height: 0;
        margin-right: 8px;
    }

    &__text {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
}
