.yc-round-icon {
    $class: &;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;

    line-height: 32px;
    font-size: 12px;
    text-transform: uppercase;
    text-align: center;
    vertical-align: middle;
    font-weight: 500;
    color: var(--yc-color-service-icon);

    border-radius: 50%;
    background: var(--yc-color-contrast-70-solid);

    &_color_red {
        background-color: var(--yc-color-scarlet-solid);
    }

    &_color_yellow {
        background-color: var(--yc-color-saffron-solid);
    }

    &_color_blue {
        background-color: var(--yc-color-azure-solid);
    }

    &_color_green {
        background-color: var(--yc-color-apple-solid);
    }
}
