@mixin button2_theme_flat-special {
    .button2_theme_flat-special {
        position: relative;
        z-index: 0;

        color: var(--yc-color-button-flat-special);
        border: none;
        outline: none;
    }

    .button2_theme_flat-special.button2_checked_yes:not(.button2_disabled_yes) {
        .button2__text,
        .button2__icon {
            opacity: 0.8;
        }
    }

    .button2_theme_flat-special {
        .button2__text,
        .button2__icon {
            transition: opacity 0.1s ease-out;
        }
    }

    .button2_theme_flat-special.button2_hovered_yes {
        .button2__text,
        .button2__icon {
            color: var(--yc-color-button-flat-special);
        }

        &:before {
            background-color: var(--yc-color-button-flat-special-background-hover);
        }
    }

    .button2_theme_flat-special.button2_view_default.button2_tone_default.button2_disabled_yes {
        .button2__text,
        .button2__icon {
            color: var(--yc-color-button-special-disabled);
        }

        &:before {
            background-color: var(--yc-color-button-special-background-disabled);
        }
    }
}
