.modal[class] { /* 2 */
    position: fixed;
    top: 0;
    left: 0;

    overflow-y: auto;
    -webkit-overflow-scrolling: touch;

    width: 100%;
    height: 100%;

    -webkit-tap-highlight-color: rgba(0,0,0,0);
}

.modal__table {
    display: table;

    width: 100%;
    height: 100%;

    text-align: center;
}

.modal__cell {
    display: table-cell;

    vertical-align: middle;
}

.modal__content {
    display: inline-block;

    text-align: left;

    -webkit-transform: translate3d(0,0,0); /*1*/
}
