.textarea_view_default .textarea__box {
    border-width: 2px;
    border-style: solid;
    border-radius: 4px;
}

.textarea_view_default.textarea_disabled_yes .textarea__box {
    border: none;
}


/* The size of the clear icon should be 16x16 by default in most sizes */
.textarea_view_default.textarea_size_s .textarea__clear svg,
.textarea_view_default.textarea_size_xs .textarea__clear svg {
    width: 12px;
    height: 12px;
}

.textarea_view_default.textarea_size_m .textarea__clear svg {
    width: 14px;
    height: 14px;
}

.textarea_view_default .textarea__clear svg {
    width: 16px;
    height: 16px;
}

