.textarea_view_default.textarea_tone_dark.textarea_theme_normal .textarea__control {
    color: #eceff1;
}
.textarea_view_default.textarea_tone_dark.textarea_theme_normal .textarea__icon {
    color: #7296a8;
}
.textarea_view_default.textarea_tone_dark.textarea_theme_normal .textarea__clear.textarea__clear_hovered_yes,
.textarea_view_default.textarea_tone_dark.textarea_theme_normal .textarea__clear.textarea__clear:hover {
    color: #eceff1;
}
.textarea_view_default.textarea_tone_dark.textarea_theme_normal .textarea__box {
    border-color: transparent;
    background-color: #435862;
}
.textarea_view_default.textarea_tone_dark.textarea_theme_normal.textarea_hovered_yes .textarea__box {
    border-color: transparent;
    background-color: #40545d;
}
.textarea_view_default.textarea_tone_dark.textarea_theme_normal.textarea_focused_yes .textarea__box{
    border-color: #7296a8;
}
.textarea_view_default.textarea_tone_dark.textarea_theme_normal .textarea__control::placeholder {
    color: #7296a8;
}
.textarea_view_default.textarea_tone_dark.textarea_disabled_yes.textarea_theme_normal .textarea__control {
    color: #899094;
}
.textarea.textarea_view_default.textarea_tone_dark.textarea_theme_normal.textarea_disabled_yes .textarea__box {
    border-color: initial;
    background-color: #303f46;
}
.textarea_view_default.textarea_tone_dark.textarea_theme_normal.textarea_disabled_yes .textarea__control::placeholder {
    color: #576166;
}
