$btns-height: 40px;
$height: 540px;

:root {
    --yc-navigation-minimal-width: 416px;
    --yc-navigation-minimal-height: #{$height};
    --yc-navigation-minimal-max-height: 70vh;
    --yc-navigation-minimal-max-width: 80vw;
}

.yc-navigation-minimal {
    width: var(--yc-navigation-minimal-width);
    height: var(--yc-navigation-minimal-height);
    display: flex;
    flex-direction: column;
    position: relative;
    max-height: var(--yc-navigation-minimal-max-height);
    max-width: var(--yc-navigation-minimal-max-width);

    .yc-navigation {
        height: calc(100% - 40px);
    }

    &__header {
        display: flex;
        align-items: center;
        padding: 0 20px 12px;
        flex: 0 0 auto;
    }

    &__controls {
        display: flex;
        align-items: center;
    }

    &__search-place {
        flex-grow: 1;
    }

    &__create-place {
        &_type_none {
            display: none;
        }
    }

    &__icon-plus-btn {
        margin-right: 8px;
    }

    &__icon-plus-select {
        position: absolute;
        left: 2px;
        pointer-events: none;
    }

    &__btn-create-inner {
        display: flex;
        align-items: center;
    }

    &__button-done.button2_size_n {
        font-size: 15px;

        &:before {
            border-radius: 0;
        }
    }

    &__button-place {
        width: 50%;
        box-shadow: inset 0 1px 0 var(--yc-color-contrast-2); // TODO: fix color

        .button2_size_n {
            height: $btns-height;
            line-height: $btns-height;
        }
    }

    &__footer {
        display: flex;
        flex: 0 0 auto;
    }

    &__popup {
        border-radius: 4px;
        overflow: hidden;
        background-color: var(--yc-color-base);
    }
}
