@import '../../../styles/mixins.scss';

.yc-share-tooltip {
    position: relative;

    &__icon {
        cursor: pointer;
        color: var(--yc-color-text-inactive);

        &:hover {
            color: var(--yc-color-text-primary);
        }
    }

    &__tooltip {
        padding: 8px;
        max-width: none;

        &.tooltip_view_classic.tooltip_theme_promo {
            .button2_size_s {
                height: 28px;
                font-size: 13px;
                line-height: 28px;
            }
        }
    }
}
