@import './yc-socials.scss';
@import './yc-theme-dark';
@import './yc-theme-light';

.yc-root {
    --yc-text-body-font-size: 13px;
    --yc-text-body-line-height: 16px;

    --yc-text-body2-font-size: 15px;
    --yc-text-body2-line-height: 20px;

    --yc-text-lead-font-size: 18px;
    --yc-text-lead-line-height: 24px;

    --yc-text-header-font-size: 20px;
    --yc-text-header-line-height: 24px;

    --yc-text-title-font-size: 24px;
    --yc-text-title-line-height: 28px;

    --yc-font-family: 'YS Text', 'Helvetica Neue', Arial, Helvetica, sans-serif;

    --yc-scrollbar-width: 12px;


    font-family: var(--yc-font-family);
    font-size: var(--yc-text-body-font-size);
    line-height: var(--yc-text-body-line-height);

    @include yc-socials;

    &_theme_light {
        @include yc-theme-light;

        background: var(--yc-color-base);
        color: var(--yc-color-text-primary);
    }

    &_theme_dark {
        @include yc-theme-dark;

        background: var(--yc-color-base);
        color: var(--yc-color-text-primary);
    }
}

body.yc-root {
    &::-webkit-scrollbar, *::-webkit-scrollbar {
        width: var(--yc-scrollbar-width);
        height: var(--yc-scrollbar-width);
        background: var(--yc-color-scroll-track);
    }

    &::-webkit-scrollbar-track, *::-webkit-scrollbar-track {
        background: var(--yc-color-scroll-track);
    }

    &::-webkit-scrollbar-corner, *::-webkit-scrollbar-corner {
        background: var(--yc-color-scroll-corner);
    }

    &::-webkit-scrollbar-thumb, *::-webkit-scrollbar-thumb {
        background: var(--yc-color-scroll-handle);
    }

    &::-webkit-scrollbar-thumb:hover, *::-webkit-scrollbar-thumb:hover {
        background: var(--yc-color-scroll-handle-hover);
    }

    scrollbar-width: var(--yc-scrollbar-width);
    scrollbar-color: var(--yc-color-scroll-handle) var(--yc-color-scroll-track);
}
