.yc-table-column-setup {
    &__controls {
        padding: 4px 4px 0;
    }

    &__action-button {
        width: 100%;
    }

    &__control-button {
        padding: 0 12px;
        display: flex;
        align-items: center;

        .button2__text {
            margin-left: 2px;
            margin-right: 0;
        }
    }

    &__status {
        margin-left: 5px;
        color: var(--yc-color-text-secondary);
    }

    &__tick-wrap {
        position: absolute;
        left: 14px;
        top: 50%;
        transform: translateY(-50%);
        width: 10px;
        height: 10px;
        visibility: hidden;

        &_visible {
            visibility: visible;
        }
    }

    &__tick {
        color: var(--yc-color-special-area);
    }

    &__lock-wrap {
        position: absolute;
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
        color: var(--yc-color-text-hint);
    }

    &__title {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }

    &__popup {
        padding: 4px 0;
    }

    &__item {
        padding: 0 8px 0 32px;
        cursor: pointer;

        & .yc-list__item-sort-icon {
            cursor: move;
        }
    }
}
