.yc-root {
    .yc-field-wrapper {
        display: inline-block;
        width: 100%;
        vertical-align: top;

        & > * {
            vertical-align: top;
        }

        &__error-text {
            color: var(--yc-color-text-danger);
            margin-top: 2px;
            font-size: var(--yc-text-body-font-size);
            line-height: var(--yc-text-body-line-height);
        }

        &_state_error {
            .textinput.textinput_view_default.textinput_tone_default.textinput_theme_normal .textinput__box,
            .textinput.textinput_view_default.textinput_tone_default.textinput_theme_normal.textinput_hovered_yes .textinput__box,
            .textinput.textinput_view_default.textinput_tone_default.textinput_theme_normal.textinput_focused_yes .textinput__box,
            .textarea.textarea_theme_normal .textarea__box,
            .textarea.textarea_theme_normal.textarea_hovered_yes .textarea__box,
            .textarea.textarea_theme_normal.textarea_focused_yes .textarea__box {
                border-color: var(--yc-color-text-danger);
            }
        }
    }
}
