.yc-suggest {
    &__input {}

    &__popup {}

    &__loader {
        display: flex;
        justify-content: center;
        padding: 10px;
    }

    &__list {
        display: flex;
        max-height: 60vh;
        overflow-y: auto;
        overflow-x: hidden;

        &_empty {
            text-align: center;
            padding: 12px;
        }
    }

    &__item {
        width: 100%;
        overflow: hidden;
    }
}
