.yc-search {
    flex-grow: 1;
    flex-shrink: 1;
    margin-right: 30px;

    &__items-container {
        .yc-list__item {
            &_active {
                background-color: var(--yc-color-base);
            }

            &:first-child {
                margin-top: 12px;
            }

            &:last-child {
                margin-bottom: 12px;
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;
        height: 40px;
        min-width: 516px;
        padding: 0 20px;
        color: inherit;
        text-decoration: none;
        cursor: pointer;

        &:hover {
            background-color: var(--yc-color-hover-area);
        }
    }

    &__icon {
        color: var(--yc-color-icon);
        display: flex;
        justify-content: center;
        flex: 0 0 24px;
        align-items: center;
        margin-right: 12px;
        overflow: hidden;
    }

    &__title {
        flex: 1 1 auto;
        overflow: hidden;
    }

    &__meta {
        color: var(--yc-color-text-secondary);
        flex: 0 0 120px;
        padding: 0 10px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }
}
