.yc-datepicker-popup-content {
    display: flex;
    flex-direction: column;
    height: 100%;

    &__tabs {
        flex: 0 0 40px;
        padding-left: 16px;

        & .yc-tabs {
            box-shadow: none;
        }
    }

    &__calendar-container {
        flex: 1 1 auto;
        overflow-y: auto;
    }

    &__apply-btn {
        flex: 0 0 40px;
        padding: 2px;
    }
}
