.yc-root {
    --yc-tabs-color-divider: var(--yc-color-divider);
    --yc-tabs-color-border-active: var(--yc-color-azure-solid);
    --yc-tabs-color-text-active: var(--yc-color-text-primary);
    --yc-tabs-color-text-inactive: var(--yc-color-text-complementary);
    --yc-tabs-color-text-disabled: var(--yc-color-text-inactive);
    --yc-tabs-space-between: 40px;
    --yc-tabs-border-width: 2px;
    --yc-tabs-height: 36px;
}

.yc-tabs {
    display: flex;
    align-items: flex-end;
    flex-wrap: wrap;
    box-shadow: inset 0 -1px 0 0 var(--yc-tabs-color-divider);
    overflow: hidden;

    & > * {
        &:not(:last-child) {
            margin-right: var(--yc-tabs-space-between);
        }
    }

    &__tab {
        border-bottom: var(--yc-tabs-border-width) solid transparent;
        padding: var(--yc-tabs-border-width) 1px 0;
        line-height: var(--yc-tabs-height);
        box-sizing: border-box;
        text-decoration: none;
        color: var(--yc-tabs-color-text-inactive);

        cursor: pointer;
        user-select: none;
        outline: none;
        white-space: nowrap;

        &_active {
            border-color: var(--yc-tabs-color-border-active);
            color: var(--yc-tabs-color-text-active);
        }

        &_disabled {
            pointer-events: none;
            color: var(--yc-tabs-color-text-disabled);
        }
    }
}
