.yc-select-item {
    display: flex;
    align-items: center;
    position: relative;
    padding: 0 12px 0 36px;
    font-size: 13px;
    line-height: 28px;
    min-height: 28px;
    user-select: none;
    cursor: pointer;
    margin-top: 4px;

    &__tick-wrap {
        position: absolute;
        left: 14px;
        top: 50%;
        transform: translateY(-50%);
        width: 10px;
        height: 10px;
    }

    &__tick {
        color: var(--yc-color-special-area);
    }

    &_show-meta {
        line-height: 16px;
        padding-top: 8px;
        padding-bottom: 8px;
    }

    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;

        &_small {
            width: 16px;
            height: 16px;
            margin-right: 6px;
        }

        &_large {
            width: 24px;
            height: 24px;
            margin-right: 10px;
        }
    }

    &__info {
        flex: 1 1 auto;
        overflow: hidden;
    }

    &__title,
    &__meta {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
    }

    &__meta {
        color: var(--yc-color-text-secondary);
    }

    &__only-btn {
        display: none;
    }

    &:last-child {
        margin-bottom: 4px;
        overflow: hidden;
    }

    &:hover {
        background-color: var(--yc-color-hover-area);
    }

    &:hover &__only-btn {
        display: block;
    }

    &_disabled {
        color: var(--yc-color-text-inactive);
        pointer-events: none;
    }
}
