.textarea__clear {
    z-index: 3; /*   __box  __control */
    top: 0;
    right: 0;

    visibility: hidden;

    cursor: pointer;

    opacity: 0;
}

.textarea_has-clear_yes .textarea__clear {
    position: absolute;
}

.textarea .textarea__clear_visible_yes {
    visibility: visible;

    opacity: .3;
}

.textarea_disabled_yes .textarea__clear_visible_yes {
    z-index: 0; /*   __control  __box */

    opacity: .1;
}
