@import "common/mixins";

.ch-issue {
  font-size: 14px;
  line-height: 18px;
  padding: 20px 0;
  margin-left: 20px;

  &__line {
    margin: 0 0 10px;
    padding: 0 10px 0 0;
    display: flex;
    align-items: flex-start;
  }

  &__icon {
    display: flex;
    flex: 0 0 24px;
    width: 24px;
    height: 24px;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    color: var(--yc-color-control-accent);
  }

  &__message {
    margin-left: 10px;
    padding: 3px 0;
    margin-right: auto;
    display: flex;
  }

  &__message-text {
    flex: 1 1 auto;
    white-space: pre-wrap;
    word-break: break-word;
    min-width: 240px;
  }

}
