@import '../../../styles/mixins';

$serviceMenuWidth: 295px;
$serviceMenuSidePadding: 24px;
$serviceMenuItemHeight: 40px;

.yc-service-menu {
    $_: &;

    width: $serviceMenuWidth;
    max-width: 100%;
    --yc-font-size-service-menu-title: 15px;
    --yc-font-size-service-menu-item: 13px;
    --yc-color-service-menu-group-title: var(--yc-color-text-primary);
    --yc-color-service-menu-item: var(--yc-color-text-complementary);
    padding-bottom: 20px;

    &__input-place {
        padding: 20px $serviceMenuSidePadding 8px;
    }

    &__common {
        --yc-font-size-service-menu-item: 15px;
        --yc-color-service-menu-item: var(--yc-color-text-primary);

        margin-top: 8px;
        border-top: 1px solid var(--yc-color-divider);
        padding-top: 8px;

        &:empty {
            display: none;
        }
    }

    &__items {
        overflow: hidden;
        transition: height 0.1s ease-in-out;
    }

    &__group-title,
    &__item {
        display: flex;
        align-items: center;
        padding: 0 $serviceMenuSidePadding;
        height: $serviceMenuItemHeight;
        cursor: pointer;

        &:hover {
            background-color: var(--yc-color-hover-area);
        }

        &_current {
            background-color: var(--yc-color-selection);
        }
    }

    &__group-title {
        font-size: var(--yc-font-size-service-menu-title);
        color: var(--yc-color-service-menu-group-title);
        font-weight: 500;
    }

    &__item {
        color: var(--yc-color-service-menu-item);
        font-size: var(--yc-font-size-service-menu-item);
    }

    &__item-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        margin-right: 12px;

        &:empty {
            display: none;
        }
    }

    &__item-wrapper {
        &_disabled {
            --yc-color-service-menu-group-title: var(--yc-color-text-inactive);
            --yc-color-service-menu-item: var(--yc-color-text-inactive);
            pointer-events: none;
            cursor: default;
        }
    }

    &__item-text {
        @include overflow-ellipsis();
    }

    &__alpha {
        font-size: 15px;
        font-weight: 500;
        margin-left: 2px;
        transform: translate(0, -6px);
        color: var(--yc-color-text-danger);
    }
}
