.textarea_theme_normal {
    position: relative;

    display: inline-block;

    box-sizing: border-box;
    width: 100%;
}

.textarea_theme_normal .textarea__control {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;

    color: #000;
}

.textarea_theme_normal .textarea__clear_visible_yes.textarea__clear_hovered_yes,
.textarea_theme_normal .textarea__clear_visible_yes:hover {
    opacity: 1;
}

.textarea_disabled_yes .textarea__clear_visible_yes {
    cursor: default;
}

.textarea_view_classic.textarea_disabled_yes.textarea_theme_normal .textarea__control {
    color: #aaa;
    -webkit-text-fill-color: #aaa; /* https://st.yandex-team.ru/ISL-2132 Safari needs it to blend color properly */
}

.textarea_view_classic.textarea_theme_normal .textarea__control::placeholder {
    color: #999;
}

.textarea_view_classic.textarea_theme_normal .textarea__box {
    border: 1px solid rgba(0,0,0,.2);
    background-color: #fff;
}

.textarea_view_classic.textarea_theme_normal.textarea_hovered_yes .textarea__box {
    border-color: rgba(0,0,0,.3);
}

.textarea_view_classic.textarea_theme_normal.textarea_focused_yes {
    border: 0;
}

.textarea_view_classic.textarea_theme_normal.textarea_focused_yes .textarea__box,
.textarea_view_classic.textarea_theme_normal.textarea_focused_yes.textarea_hovered_yes .textarea__box {
    border: 2px solid #ffdb4d;
}

.textarea_view_classic.textarea_theme_normal.textarea_disabled_yes .textarea__box {
    border-color: transparent;
    background-color: rgba(0,0,0,.08);
}
