.link_theme_pseudo {
    color: #04b;
}

.link_theme_pseudo .link__inner {
    border-bottom: 1px dotted;
}

.utilityfocus .link_theme_pseudo:focus {
    border-bottom: 1px dotted;
    outline: none;
    box-shadow: 0 0 0 2px #fc0;
}

.utilityfocus .link_theme_pseudo:focus .link__inner {
    border-bottom: none;
}
